html, body {
  background:darkcyan;
  color:black;
  margin:0;
  padding: 0;
  padding-top: 10px;
  font-family: 'Courier New', Courier, monospace;
  text-align: center;
}

body {
  font-weight: 400;
}

.button {
  font-weight: 700;
  font-size: 15px;
  letter-spacing: 1px;
  
}
button {
  display:inline-block;
  margin: 0 auto;
  border-radius: 20px;
  background: lightgreen;
  padding: 10px 20px;
  text-transform: uppercase;
}

h1 {
  font-size: 20px;
  text-align:center;
}

textarea {
  border: 3px solid black;
  width: 60%;
  margin: 0 auto;
  outline: none;
  height: 200px;
  background:lightblue;
  padding: 10px;
}
